.top-bar-logo.hide-for-small-only {
	
	background-color: #fff;
	margin-top: -4.2rem;
	margin-left: 0;
	padding: .5rem;
	
	border-bottom-left-radius: .5rem;
	border-bottom-right-radius: .5rem;
	
	position: relative;
	z-index: 42;
	
	//border: 0.25px $primary-color solid;
	border-top: none;
	
	-webkit-box-shadow: 0 0 0.25em $primary-color;
        box-shadow: 0 0 0.25em $primary-color;
	
}

.top-bar-logo-no-overlap {

	background-color: #fff;
	width: auto;
	padding: 0.25rem;

	a {
		bottom: 0;
		padding: .7rem .25rem;
	}

}